<template>
    <div>
      <h3></h3>
      <template>
        <el-transfer v-model="value1" :data="data"></el-transfer>
      </template>
      <h3></h3>
      <p></p>
      <template>
        <el-transfer
          filterable
          :filter-method="filterMethod"
          filter-placeholder=""
          v-model="value2"
          :data="data2">
        </el-transfer>
      </template>
      <h3></h3>
      <p></p>
      <template>
        <p style="text-align: center; margin: 0 0 20px"> render-content </p>
        <div style="text-align: center">
          <el-transfer
            style="text-align: left; display: inline-block"
            v-model="value3"
            filterable
            :left-default-checked="[2, 3]"
            :right-default-checked="[1]"
            :render-content="renderFunc"
            :titles="['Source', 'Target']"
            :button-texts="['', '']"
            :format="{
        noChecked: '${total}',
        hasChecked: '${checked}/${total}'
      }"
            @change="handleChange"
            :data="data3">
            <el-button class="transfer-footer" slot="left-footer" size="small"></el-button>
            <el-button class="transfer-footer" slot="right-footer" size="small"></el-button>
          </el-transfer>
        </div>
        <p style="text-align: center; margin: 50px 0 20px"> scoped-slot </p>
        <div style="text-align: center">
          <el-transfer
            style="text-align: left; display: inline-block"
            v-model="value4"
            filterable
            :left-default-checked="[2, 3]"
            :right-default-checked="[1]"
            :titles="['Source', 'Target']"
            :button-texts="['', '']"
            :format="{
        noChecked: '${total}',
        hasChecked: '${checked}/${total}'
      }"
            @change="handleChange"
            :data="data">
            <span slot-scope="{ option }">{{ option.key }} - {{ option.label }}</span>
            <el-button class="transfer-footer" slot="left-footer" size="small"></el-button>
            <el-button class="transfer-footer" slot="right-footer" size="small"></el-button>
          </el-transfer>
        </div>
      </template>
    </div>
</template>

<script>
export default {
  name: 'transfer',
  data () {
    const generateData = _ => {
      const data = []
      for (let i = 1; i <= 15; i++) {
        data.push({
          key: i,
          label: ` ${i}`,
          disabled: i % 4 === 0
        })
      }
      return data
    }
    const generateData2 = _ => {
      const data = []
      const cities = ['', '', '', '', '', '', '']
      const pinyin = ['shanghai', 'beijing', 'guangzhou', 'shenzhen', 'nanjing', 'xian', 'chengdu']
      cities.forEach((city, index) => {
        data.push({
          label: city,
          key: index,
          pinyin: pinyin[index]
        })
      })
      return data
    }
    const generateData3 = _ => {
      const data = []
      for (let i = 1; i <= 15; i++) {
        data.push({
          key: i,
          label: ` ${i}`,
          disabled: i % 4 === 0
        })
      }
      return data
    }
    return {
      data: generateData(),
      value1: [1, 4],
      data2: generateData2(),
      value2: [],
      filterMethod (query, item) {
        return item.pinyin.indexOf(query) > -1
      },
      data3: generateData3(),
      value3: [1],
      value4: [1],
      renderFunc (h, option) {
        return <span>{ option.key } - { option.label }</span>
      }
    }
  },
  methods: {
    handleChange (value, direction, movedKeys) {
      console.log(value, direction, movedKeys)
    }
  }
}
</script>

<style scoped>
h3{
  margin: 55px 0 20px;
  font-weight: 400;
  color: #1f2f3d;
  font-size: 22px;
}
p{
  font-size: 14px;
  color: #5e6d82;
  line-height: 1.5em;
}
.transfer-footer {
  margin-left: 20px;
  padding: 6px 5px;
}
</style>



