<template>
  <div style="margin-top: 50px">
    <h3></h3>
    <p> "{{$store.getters.info.permissions}}"</p>
    <p><el-button type="primary" plain size="medium"></el-button></p>
    <p> [""]<el-button type="primary" plain size="medium" v-role-btn="['admin']"></el-button></p>
    <p> [""]<el-button type="danger" plain size="medium" v-role-btn="['superAdmin']"></el-button></p>
    <p> ["", ""]<el-button type="primary" plain size="medium" v-role-btn="['superAdmin','admin']"></el-button></p>
  </div>
</template>

<script>
export default {
  name: 'btnPermissions'
}
</script>

<style scoped>
  p{
    margin: 20px 0px;
  }
</style>



