<template>
  <div>
    <el-radio v-model="permissions" label="superAdmin" border></el-radio>
    <el-radio v-model="permissions" label="admin" border></el-radio>
    <el-radio v-model="permissions" label="ordinary" border></el-radio>
    <div style="margin: 50px 0px;">
      [:  "{{options.permissions}}",    :   "{{options.role}}"]
    </div>
    <h3></h3>
    <p>[""]</p>
    <p>["", ]</p>
    <p>[""]</p>
  </div>
</template>

<script>
export default {
  name: 'pagePermissions',
  data () {
    return {
      permissions: this.$store.getters.info.role,
      options: {
        role: this.$store.getters.info.role,
        permissions: this.$store.getters.info.permissions
      }
    }
  },
  mounted () {
    this.$store.dispatch('setRole', this.options)
  },
  watch: {
    permissions (newQuestion, oldQuestion) {
      switch (newQuestion) {
        case 'superAdmin':
          this.options = {
            role: 'superAdmin',
            permissions: ''
          }
          break
        case 'admin':
          this.options = {
            role: 'admin',
            permissions: ''
          }
          break
        case 'ordinary':
          this.options = {
            role: 'ordinary',
            permissions: ''
          }
      }
      this.$store.dispatch('setRole', this.options)

      //   tabnav 
      this.$store.dispatch('removeOtherTab', {tabItem: {
        title: 'pageControl',
        path: '/pagePermissions'
      }, router: this.$router})
    }
  }
}
</script>

<style scoped>
p{
  margin: 20px 0px;
}
</style>



