<template>
  <div>
    <h3></h3>
    <p></p>
    <template>
      <el-table
        :data="tableData5"
        style="width: 100%">
        <el-table-column
          prop="date"
          label=""
          sortable
          width="180"
          :filters="[{text: '2016-05-01', value: '2016-05-01'}, {text: '2016-05-02', value: '2016-05-02'}, {text: '2016-05-03', value: '2016-05-03'}, {text: '2016-05-04', value: '2016-05-04'}]"
          :filter-method="filterHandler"
        >
        </el-table-column>
        <el-table-column
          prop="name"
          label=""
          width="180">
        </el-table-column>
        <el-table-column
          prop="address"
          label=""
          :formatter="formatter">
        </el-table-column>
        <el-table-column
          prop="tag"
          label=""
          width="100"
          :filters="[{ text: '', value: '' }, { text: '', value: '' }]"
          :filter-method="filterTag"
          filter-placement="bottom-end">
          <template slot-scope="scope">
            <el-tag
              :type="scope.row.tag === '' ? 'primary' : 'success'"
              disable-transitions>{{scope.row.tag}}</el-tag>
          </template>
        </el-table-column>
        <el-table-column label="">
          <template slot-scope="scope">
            <el-button
              size="mini"
              v-role-btn="['/filterTable', 'add']"
              @click="handleEdit(scope.$index, scope.row)"></el-button>
            <el-button
              size="mini"
              v-role-btn="['/filterTable', 'edit']"
              @click="handleEdit(scope.$index, scope.row)"></el-button>
            <el-button
              size="mini"
              type="danger"
              v-role-btn="['/filterTable', 'del']"
              @click="handleDelete(scope.$index, scope.row)"></el-button>
          </template>
        </el-table-column>
      </el-table>
    </template>
  </div>
</template>

<script>
export default {
  name: 'filterTable',
  data () {
    return {
      tableData5: [{
        date: '2016-05-02',
        name: '',
        address: ' 1518 ',
        tag: ''
      }, {
        date: '2016-05-04',
        name: '',
        address: ' 1517 ',
        tag: ''
      }, {
        date: '2016-05-01',
        name: '',
        address: ' 1519 ',
        tag: ''
      }, {
        date: '2016-05-03',
        name: '',
        address: ' 1516 ',
        tag: ''
      }]
    }
  },
  methods: {
    handleEdit (index, row) {
      console.log(index, row)
      this.$message({
        showClose: true,
        message: index, row,
        type: 'success'
      })
    },
    handleDelete (index, row) {
      console.log(index, row)
      this.$message({
        showClose: true,
        message: index, row,
        type: 'success'
      })
    },
    filterHandler (value, row, column) {
      const property = column['property']
      return row[property] === value
    },
    formatter (row, column) {
      return row.address
    },
    filterTag (value, row) {
      return row.tag === value
    }
  }
}
</script>

<style scoped>

</style>



